import _ from 'lodash';
var formatters = require('./formatters');

var _loader; // hide loader whenever you want
var methods = {
	showLoading: function() {
		if (!_loader) {
			_loader = this.$loading.show();
		}
	},
	hideLoading: function() {
		if (_loader) {
			_loader.hide();
		}
	}
};

_.merge(methods, formatters);

export default {
	methods: methods
};
