<style lang="scss">
@import '@/assets/scss/_variables.scss';
</style>
<template>
	<div>
		<BackgroundImage></BackgroundImage>
		<div class="row">
			<div class="col-md-6 offset-md-3">
				<CurrentWeather></CurrentWeather>
			</div>
		</div>
	</div>
</template>

<script>
import CurrentWeather from '@/components/CurrentWeather.vue';
import BackgroundImage from '@/components/BackgroundImage.vue';
export default {
	data() {
		return {
			state: _WT.state
		};
	},
	created() {
		_WT.EventHub.$on('refresh-weather', this.getWeather);
	},
	methods: {
		getWeather: function() {
			var self = this;
			self.showLoading();
			_WT.weather.openWeather.get().then(function(data) {
				self.state.weather = data;
				self.hideLoading();
			});
		}
	},
	components: {
		CurrentWeather,
		BackgroundImage
	}
};
</script>
