<style lang="scss">
@import '@/assets/scss/_variables.scss';
.background-image {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: $black;
	background-repeat: no-repeat;
	background-position: center center;
	// background-color: transparent;
	background-size: cover;
	animation: fadein 2s;
}

@keyframes fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
</style>
<template>
<div class="background-image" :style="backgroundStyles"></div>
</template>

<script>
export default {
	data() {
		return {
			state: _WT.state,
			backgroundStyles: {
				backgroundImage: null
			}
		};
	},
	created() {
		// this.randomBackground();
	},
	computed: {},
	methods: {
		randomBackground: function(conditionIcon) {
			var imageCount = 5;

			var random = Math.floor(Math.random() * imageCount) + 1;

			if (conditionIcon === 'rain') {
				random = conditionIcon + '-' + random;
			}

			if (conditionIcon === 'clear-day') {
				random = 'sunny-' + random;
			}

			if (conditionIcon.includes('cloudy')) {
				random = 'cloudy-' + random;
			}

			if (conditionIcon == 'snow' || conditionIcon == 'sleet') {
				random = 'snow-' + random;
			}

			this.backgroundStyles.backgroundImage = 'url("/img/background/' + random + '.jpg")';
		}
	},
	filters: {},
	watch: {
		'state.weather.current': function(newValue) {
			if (newValue.icon) {
				this.randomBackground(newValue.icon);
			}
		}
	}
};
</script>
