var _locationKey = 'locationKey';

var methods = {
	getLocation() {
		return methods.getLocalStorageLocation().catch(methods.getCurrentLocation);
	},
	getCityState: function(coords) {
		return new Promise(function(resolve, reject) {
			var openstreetmapUrl = `https://nominatim.openstreetmap.org/reverse?format=json&lat=${coords.latitude}&lon=${coords.longitude}`;
			$.ajax({
				url: openstreetmapUrl,
				success: function(data) {
					var locationName = 'Unknown Location';
					if (data && data.address) {
						var firstPart = '';
						if (data.address.city) {
							firstPart = data.address.city + ', ';
						} else if (data.address.town) {
							firstPart = data.address.town + ', ';
						} else if (data.address.village) {
							firstPart = data.address.village + ', ';
						}

						locationName = firstPart + data.address.state;
					}

					resolve({
						latitude: coords.latitude,
						longitude: coords.longitude,
						name: locationName
					});
				},
				//Try geocode if api fails
				error: function(xhr, status, error) {
					//If geocode fails just resolve unknown location
					resolve({
						latitude: coords.latitude,
						longitude: coords.longitude,
						name: 'Unknown Location'
					});
				}
			});
		});
	},
	noBrowserGeoLocation: function(resolve) {
		var data = {};

		data.coords = {
			longitude: null,
			latitude: null
		};

		data.locationName = null;
		resolve(data);
	},
	getCurrentLocation: function() {
		return new Promise(function(resolve, reject) {
			if (navigator && navigator.geolocation) {
				navigator.geolocation.getCurrentPosition(
					function(location) {
						var coords = location.coords;
						methods.getCityState(coords).then(data => resolve(data));
					},
					function() {
						methods.noBrowserGeoLocation(resolve);
					}
				);
			} else {
				methods.noBrowserGeoLocation(resolve);
			}
		});
	},
	getLocalStorageLocation: function() {
		return new Promise(function(resolve, reject) {
			var location = _WT.ls.get(_locationKey);
			if (location) {
				resolve(location);
			} else {
				reject();
			}
		});
	},
	setLocalStorageLocation: function(location) {
		_WT.state.location = location;
		return _WT.ls.set(_locationKey, location);
	}
};

export default methods;
