import moment from 'moment';

_WT.weather.openWeather = (function() {
	var convertBadgeIconCode = function(code) {
		var badgeIcon;
		switch (code) {
			case '01d':
				badgeIcon = 'clear-day';
				break;
			case '01n':
				badgeIcon = 'clear-night';
				break;
			case '10n':
			case '10d':
			case '09d':
			case '09n':
			case '11d':
			case '11n':
				badgeIcon = 'rain';
				break;
			case '13n':
			case '13d':
				badgeIcon = 'snow';
				break;
			case '50n':
			case '50d':
				badgeIcon = 'fog';
				break;
			case '04d':
			case '04n':
			case '02d':
			case '02n':
				badgeIcon = 'cloudy';
				break;
			case '03d':
				badgeIcon = 'partly-cloudy-day';
				break;
			case '03n':
				badgeIcon = 'partly-cloudy-night';
				break;
			default:
				badgeIcon = 'clear-day';
		}

		return badgeIcon;
	};

	var formatData = function(data) {
		var result = {
			current: {
				temperature: null,
				highTemperature: null,
				lowTemperature: null,
				summary: null,
				icon: null
			},
			forecasts: []
		};

		if (data && data.list && data.list.length > 0) {
			var currentDay = data.list[0];

			if (currentDay) {
				if (currentDay.weather && currentDay.weather.length > 0) {
					result.current.summary = currentDay.weather[0].description;
					result.current.icon = convertBadgeIconCode(currentDay.weather[0].icon);
				}

				if (currentDay.temp) {
					result.current.temperature = currentDay.temp.day;
					result.current.highTemperature = currentDay.temp.max;
					result.current.lowTemperature = currentDay.temp.min;
				}
			}

			for (var i = 1; i < data.list.length; i++) {
				var forecastVal = data.list[i];
				result.forecasts.push({
					date: moment.unix(forecastVal.dt),
					highTemperature: forecastVal.temp.max,
					lowTemperature: forecastVal.temp.min,
					icon: forecastVal.weather && forecastVal.weather.length > 0 ? convertBadgeIconCode(forecastVal.weather[0].icon) : null
				});
			}
		}

		return result;
	};

	var get = function() {
		return new Promise(function(resolve, reject) {
			var location = _WT.state.location;
			var _weatherKey = `weather.${location.longitude}.${location.latitude}`;

			var cachedWeather = _WT.ls.get(_weatherKey);
			if (cachedWeather) {
				resolve(cachedWeather);
				return;
			}

			$.ajax({
				url: `https://api.openweathermap.org/data/2.5/forecast/daily?lat=${location.latitude}&lon=${location.longitude}&appid=61f9b9306648d74a4ffb781d9f569a93&cnt=5&units=imperial`,
				dataType: process.env.NODE_ENV === 'development' ? 'jsonp' : 'json',
				success: function(data) {
					data = formatData(data);
					_WT.ls.set(_weatherKey, data, 600000); //10 minutes
					resolve(data);
				}
			});
		});
	};

	return {
		get: get
	};
})();
