import Vue from 'vue';

Vue.filter('truncate', function(str, length, ellipses) {
	length = length || 30;

	if (!str || str.length <= length) {
		return str;
	}

	if (ellipses === undefined) {
		ellipses = '...';
	}
	ellipses = '' + ellipses;

	str = str.slice(0, length);

	return str + ellipses;
});

Vue.filter('formatTemp', function(temp) {
	var formatters = require('./formatters');
	return formatters.formatTemp(temp, _WT.state.settings.tempScale);
});
