<style lang="scss">
@import '@/assets/scss/_variables.scss';

.location-bar {
	&:hover {
		text-decoration: underline;
	}
	.map-icon {
		font-size: 1rem;
		line-height: inherit;
	}
}

.location-picker-wrapper {
	position: absolute;
	top: 40px;
	right: 0;
	left: 0;
	z-index: 1;
	width: 80%;
	margin-right: auto;
	margin-left: auto;
	background-color: #f8f9fa;
	border-radius: 0.25rem;
	box-shadow: 0 50px 40px rgba(0, 0, 0, 0.5);
	.location-search {
		padding: 10px 15px 0;
		margin-bottom: 0;
	}

	.locationList {
		height: 200px;
		padding: 0 0 10px 15px;
		margin-bottom: 0;
		overflow-y: auto;
		color: $black;
		li {
			display: flex;
			margin-top: 10px;
			.locationName {
				flex-grow: 1;
			}
			&:hover {
				color: lighten($black, 60%);
			}
		}
	}
}
</style>

<template>
	<div>
		<div class="location-bar clickable" @click.prevent="openLocationPicker()">
			<i class="fa fa-map-marker mr-1 map-icon" aria-hidden="true"></i>
			<span>{{state.location.name | truncate(40)}}</span>
		</div>

		<div v-on-clickaway="clickOffLocationPicker" class="location-picker-wrapper" v-if="locationOpen">
			<div class="form-group location-search">
				<input type="text" class="form-control" v-focus v-model="searchLocation" placeholder="City, State, or Zip Code">
			</div>
			<ul class="list-unstyled locationList">
				<li :key="location.woeid" v-for="location in locationSearchResults">
					<div class="locationName clickable" v-on:click="setLocation(location)">{{location.name}}</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
var _ = require('lodash');
import locationHelper from '@/helpers/location';
export default {
	data() {
		return {
			locationOpen: false,
			state: _WT.state,
			locationSearchResults: [],
			searchLocation: null
		};
	},
	created() {
		this.showLoading();
		locationHelper.getLocation().then(function(location) {
			locationHelper.setLocalStorageLocation(location);
			_WT.EventHub.$emit('refresh-weather');
		});
	},
	computed: {},
	methods: {
		clickOffLocationPicker: function() {
			this.locationOpen = false;
		},
		openLocationPicker: function() {
			var self = this;
			self.locationOpen = !self.locationOpen;
		},
		setLocation: function(location) {
			this.state.location = location;
			this.locationOpen = false;
			locationHelper.setLocalStorageLocation(location);
			_WT.EventHub.$emit('refresh-weather');
		},
		searchLocationInput: _.debounce(function() {
			var self = this;
			self.locationSearchResults = [];
			if (!self.searchLocation) {
				return;
			}

			$.ajax({
				url: `https://nominatim.openstreetmap.org/search?q=${self.searchLocation}&format=json&polygon=1&addressdetails=1&dedupe=1&polygon_geojson=0&polygon_kml=0&polygon_geojson=0&extratags=0&polygon=0`,
				// dataType: process.env.NODE_ENV === 'development' ? 'jsonp' : 'json',
				success: function(data) {
					if (data && data.length > 0) {
						_.forEach(data, function(result) {
							var name = '';
							if (result.address.city) {
								name = result.address.city;
							}

							if (result.address.city && result.address.state) {
								name += ', ';
							}

							if (result.address.state) {
								name += result.address.state;
							}

							if (result.address.country && result.address.country !== 'USA') {
								name += ' ' + result.address.country;
							}
							var location = {
								name: name,
								latitude: result.lat,
								longitude: result.lon
							};

							self.locationSearchResults.push(location);
						});
					}
				},
				error: function(data, test, test2) {
					//Ignore errors
				}
			});
		}, 300)
	},
	watch: {
		searchLocation: function() {
			this.searchLocationInput();
		}
	}
};
</script>
