Vue.directive('title', {
	inserted: (el, binding) => (document.title = _WT.state.app.title),
	update: (el, binding) => (document.title = _WT.state.app.title)
});

import Skycons from 'skycons';

Vue.directive('skyCon', {
	inserted: function(el) {
		var currentSkycon = new Skycons({
			color: 'black',
			resizeClear: true
		});

		currentSkycon.add('current-icon', 'rain');
		currentSkycon.play();
	},
	update: function(el) {
		var currentSkycon = new Skycons({
			color: 'black',
			resizeClear: true
		});

		currentSkycon.add('current-icon', 'rain');
		currentSkycon.play();
	}
});
