_WT.weather.yahoo = (function() {
	var convertYahooBadgeIconCode = function(code) {
		// Code Description
		// 0	tornado
		// 1	tropical storm
		// 2	hurricane
		// 3	severe thunderstorms
		// 4	thunderstorms
		// 5	mixed rain and snow
		// 6	mixed rain and sleet
		// 7	mixed snow and sleet
		// 8	freezing drizzle
		// 9	drizzle
		// 10	freezing rain
		// 11	showers
		// 12	showers
		// 13	snow flurries
		// 14	light snow showers
		// 15	blowing snow
		// 16	snow
		// 17	hail
		// 18	sleet
		// 19	dust
		// 20	foggy
		// 21	haze
		// 22	smoky
		// 23	blustery
		// 24	windy
		// 25	cold
		// 26	cloudy
		// 27	mostly cloudy (night)
		// 28	mostly cloudy (day)
		// 29	partly cloudy (night)
		// 30	partly cloudy (day)
		// 31	clear (night)
		// 32	sunny
		// 33	fair (night)
		// 34	fair (day)
		// 35	mixed rain and hail
		// 36	hot
		// 37	isolated thunderstorms
		// 38	scattered thunderstorms
		// 39	scattered thunderstorms
		// 40	scattered showers
		// 41	heavy snow
		// 42	scattered snow showers
		// 43	heavy snow
		// 44	partly cloudy
		// 45	thundershowers
		// 46	snow showers
		// 47	isolated thundershowers
		// 3200 not available
		var badgeIcon;
		switch (parseInt(code, 10)) {
			case 32:
			case 34:
			case 36:
				badgeIcon = 'clear-day';
				break;
			case 31:
			case 33:
				badgeIcon = 'clear-night';
				break;
			case 9:
			case 11:
			case 12:
				badgeIcon = 'rain';
				break;
			case 5:
			case 13:
			case 14:
			case 15:
			case 16:
			case 41:
			case 43:
			case 46:
				badgeIcon = 'snow';
				break;
			case 6:
			case 7:
			case 8:
			case 10:
			case 18:
				badgeIcon = 'sleet';
				break;
			case 24:
			case 25:
				badgeIcon = 'wind';
				break;
			case 19:
			case 20:
			case 21:
			case 22:
			case 23:
				badgeIcon = 'fog';
				break;
			case 26:
				badgeIcon = 'cloudy';
				break;
			case 28:
			case 30:
			case 44:
				badgeIcon = 'partly-cloudy-day';
				break;
			case 27:
			case 29:
				badgeIcon = 'partly-cloudy-night';
				break;
			case 17:
			case 35:
				badgeIcon = 'sleet'; //hail
				break;
			case 1:
			case 2:
			case 3:
			case 4:
			case 37:
			case 38:
			case 39:
			case 40:
			case 42:
			case 45:
			case 47:
				badgeIcon = 'rain'; //thunderstorm
				break;
			case 0:
				badgeIcon = 'wind'; //tornado
				break;
			default:
				badgeIcon = 'clear-day';
		}

		return badgeIcon;
	};

	var formatData = function(data) {
		var result = {
			current: {
				temperature: null,
				highTemperature: null,
				lowTemperature: null,
				summary: null,
				icon: null
			},
			forecasts: []
		};

		if (data && data.query && data.query.count > 0 && data.query.results.channel && data.query.results.channel.item && data.query.results.channel.item.condition) {
			var condition = data.query.results.channel.item.condition;

			if (data.query.results.channel.item.forecast[0] && data.query.results.channel.item.forecast[0].high && data.query.results.channel.item.forecast[0].low) {
				var forecast = data.query.results.channel.item.forecast[0];
				result.current.highTemperature = forecast.high;
				result.current.lowTemperature = forecast.low;
			}

			if (data.query.results.channel.item.forecast[0]) {
				for (var i = 0; i < data.query.results.channel.item.forecast.length; i++) {
					var forecastVal = data.query.results.channel.item.forecast[i];
					result.forecasts.push({
						date: forecastVal.date,
						highTemperature: forecastVal.high,
						lowTemperature: forecastVal.low,
						icon: convertYahooBadgeIconCode(forecastVal.code)
					});
				}
			}

			if (condition.text) {
				result.current.summary = condition.text;
			}

			if (condition.temp) {
				result.current.temperature = condition.temp;
			}

			if (condition.code) {
				result.current.icon = convertYahooBadgeIconCode(condition.code);
			}
		}

		return result;
	};

	var get = function() {
		return new Promise(function(resolve, reject) {
			var location = _WT.state.location;
			var _weatherKey = `weather.${location.longitude}.${location.latitude}`;

			var cachedWeather = _WT.ls.get(_weatherKey);
			if (cachedWeather) {
				resolve(cachedWeather);
				return;
			}

			$.ajax({
				url: `https://query.yahooapis.com/v1/public/yql?q=select * from weather.forecast where woeid in (SELECT woeid FROM geo.places WHERE text="(${location.latitude},${location.longitude})") and u="f"&format=json`,
				dataType: process.env.NODE_ENV === 'development' ? 'jsonp' : 'json',
				success: function(data) {
					data = formatData(data);
					_WT.ls.set(_weatherKey, data, 600000); //10 minutes
					resolve(data);
				}
			});
		});
	};

	return {
		get: get
	};
})();
