<style lang="scss">
@import '@/assets/scss/_variables.scss';
.weather-card {
	background-color: rgba(0, 0, 0, 0.7) !important;

	.card-header {
		border-bottom: 1px solid rgba(255, 255, 255, 0.125) !important;
		.location {
			flex-grow: 1 !important;
		}
	}

	.card-footer {
		border-top: 1px solid rgba(255, 255, 255, 0.125) !important;
	}
}
</style>
<template>
	<div class="card text-white weather-card text-overlay">
		<div class="card-header d-flex">
			<LocationPicker class="location disable-select"></LocationPicker>
			<span>
				<a href="#" class="text-white" :class="{ 'text-muted': state.settings.tempScale != 'F' }" @click.prevent="setTempScale('F')">F</a>
				<span class="text-muted">/</span>
				<a href="#" class="text-white" :class="{ 'text-muted': state.settings.tempScale != 'C' }" @click.prevent="setTempScale('C')">C</a>
			</span>
		</div>
		<div class="card-body">
			<h1 class="card-title display-1 text-center">
				{{state.weather.current.temperature | formatTemp}}°
				<!-- <skycon v-if="state.weather.current.icon" :condition="state.weather.current.icon" color="white" class="align-middle" width="80" height="80"></skycon> -->
			</h1>
			<div class="container">
				<div class="row">
					<div class="text-center col-sm" :key="index" v-for="(forecast, index) in state.weather.forecasts" v-if="index <= 4">
						<div>{{forecast.date | dateFormat}}</div>
						<skycon v-if="forecast.icon" :condition="forecast.icon" color="white" class="align-middle" width="40" height="40"></skycon>
						<div>{{forecast.highTemperature | formatTemp }}°/{{forecast.lowTemperature | formatTemp }}°</div>
					</div>
				</div>
			</div>
		</div>
		<!-- <div class="card-footer text-muted">Last updated</div> -->
	</div>
</template>

<script>
import moment from 'moment';

import LocationPicker from '@/components/LocationPicker.vue';
export default {
	data() {
		return {
			state: _WT.state
		};
	},
	computed: {},
	methods: {
		setTempScale: function(scale) {
			this.state.settings.tempScale = scale;
			_WT.ls.set('settings.tempScale', scale);
		},
		openLocationPicker: function() {}
	},
	filters: {
		dateFormat(value) {
			if (!value) {
				return '--';
			}

			return moment(value).format('ddd');
		}
	},
	watch: {},
	components: {
		LocationPicker
	}
};
</script>
